<template>
    <div class="f-flexrw">
        <div class="m-picbar">
            <swiper :options="swiperOption">
                <swiper-slide><img src="../assets/images/cont/slider_img1.png" class="swiperimg"></swiper-slide>
                <swiper-slide><img src="../assets/images/cont/slider_img2.png" class="swiperimg"></swiper-slide>
                <swiper-slide><img src="../assets/images/cont/slider_img3.png" class="swiperimg"></swiper-slide>
                <swiper-slide><img src="../assets/images/cont/slider_img4.png" class="swiperimg"></swiper-slide>
            </swiper>
            <!---->
            <!-- <div class="swiper-scrollbar"></div> //
            <div class="swiper-button-next"></div> //
            <div class="swiper-button-prev"></div> //
            <div class="swiper-pagination"></div> // -->
            <div class="swiper-pagination"></div>
        </div>
        <div class="m-listbar f-pr">
            <swiper :options="listbarOption">
                <swiper-slide>
                    <div class="listshow">
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20"></program-list>
                    </div>
                </swiper-slide>
                <swiper-slide>
                    <div class="listshow">
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20"></program-list>
                    </div>
                </swiper-slide>
                <swiper-slide>
                    <div class="listshow">
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20"></program-list>
                    </div>
                </swiper-slide>
                <swiper-slide>
                    <div class="listshow">
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20 marright20"></program-list>
                        <program-list class="marbtm20"></program-list>
                    </div>
                </swiper-slide>
            </swiper>
            <!---->
            <div class="swiper-button-next"></div>
            <div class="swiper-button-prev"></div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import ProgramList from '../components/ProgramList'
export default {
    data() {
    return {
        swiperOption: {//swiper3
            autoplay: {
                delay: 4000,//2
                speed:1500,
            },
            pagination: {
                el: '.swiper-pagination',
                type: 'bullets',
                //type: 'fraction',
                //type : 'progressbar',
                //type : 'custom',
                // progressbarOpposite: true, //Swiperdirection
                bulletElement : 'span', //HTML
                // dynamicBullets: true,  //slide
                // dynamicMainBullets: 2, //
                hideOnClick: true, //trueSwiper/
                clickable: true, //trueSwiper

            },

            loop:true,
        },
        listbarOption:{//swiper3
            autoplay: {
                delay: 5000,//2
                speed:2000,
            },
            navigation: {
                nextEl: '.swiper-button-next', //cssHTML
                prevEl: '.swiper-button-prev', //cssHTML
                hideOnClick: true, //slide/
                disabledClass: 'my-button-disabled', //
                hiddenClass: 'my-button-hidden', //Class
            },
            loop:true,
        },
    }
    },
    components: {
        swiper,
        swiperSlide,
        ProgramList
    }
}
</script>

<style lang="less">
.swiper-pagination{
    position: relative;
    top:-30px;
    right: -280px;
    width: 200px;
    .swiper-pagination-bullet{
        display: inline-block;
        width: 18px;
        height: 18px;
        margin: 0 2px;
        background-color: #fff;
        background: url(../assets/images/page_icons.png) no-repeat -855px -790px;
        cursor: pointer;
        border: none;
    }
    .swiper-pagination-bullet-active {
        opacity: 1;
        // background: #007aff;
        background: url(../assets/images/page_icons.png) no-repeat -855px -727px;
    }
}
.swiper-button-prev, .swiper-container-rtl .swiper-button-next{
    // background-color: rgba(0, 0, 0, 0.6);
    left: 10px;
    right: auto;
}
</style>



