<script type="text/ecmascript-6">
import LoginHeader from '../components/LoginHeader.vue'
import SearchNav from '../components/SearchNav'
import MainBar from '../components/MainBar'
import ContentTitle from '../components/ContentTitle'
import VideoShow from '../components/VideoShow'
import VideoList from '../components/VideoList'
import BiliFoot from '../components/BiliFoot'
import AnchorPoint from '../components/AnchorPoint'
export default {
    data() {
    return {
        activeName: 'first',
        kouweispan:1,
        rankindex:1,
        ranklistall:true,
        ranklistoriginal:false,
        dayindex:0,
        operalistnew:true,
        operalistfirst:false
    }
    },
    components: {
        LoginHeader,
        SearchNav,
        MainBar,
        ContentTitle,
        VideoShow,
        VideoList,
        BiliFoot,
        AnchorPoint
    },
    methods:{
        asb(){
            // var a=[1,2,3,4,5,6,7,8,9]
            // var b=new Array()
            // for(let i=0;i<a.length;i++){
            //     b.push(a[i])
            // }
            // console.log(''+b);
            var myDate = new Date();
            console.log( typeof(myDate) );
            
        },
        handleClick(){
            
        },
        kouweiSpan(index){
            this.kouweispan=index
        },
        rankIndex(index){
            this.rankindex=index
            if(index==1){
                this.ranklistall=true
                this.ranklistoriginal=false
            }
            if(index==2){
                this.ranklistall=false
                this.ranklistoriginal=true
            }
        },
        dayIndex(index){
            this.dayindex=index;
            if(index==0){
                this.operalistnew=true
                this.operalistfirst=false
            }else{
                this.operalistnew=false
                this.operalistfirst=true
            }
        },
        fnTextPopup (arr, options) {
        // arr
            if (!arr || !arr.length) {
            return;
            }
            // 
            var index = 0;
            document.documentElement.addEventListener('click', function (event) {
                var x = event.pageX, y = event.pageY;
                var eleText = document.createElement('span');
                eleText.className = 'text-popup';
                this.appendChild(eleText);
                if (arr[index]) {
                eleText.innerHTML = arr[index];
                } else {
                index = 0;
                eleText.innerHTML = arr[0];
                }
                // 
                eleText.addEventListener('animationend', function () {
                eleText.parentNode.removeChild(eleText);
                });
                // 
                eleText.style.left = (x - eleText.clientWidth / 2) + 'px';
                eleText.style.top = (y - eleText.clientHeight) + 'px';
                // index
                index++;
            });
        },
    },
    mounted(){
        this.asb()
        
        this.fnTextPopup(['','','']);
    }
}
</script>

<style lang="less">
/*  */
/*  */
.slide-fade-enter-active {
  transition: all 1s ease;
}
.slide-fade-leave-active {
  transition: all 1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
.text-popup {
    animation: textPopup 1s;
    color: #ffafc9;
    user-select: none;
    white-space: nowrap;
    position: absolute;
    z-index: 99;
}
@keyframes textPopup {
    0%, 100% {
    opacity: 0;
    }
    5% {
    opacity: 1;
    }
    100% {
    transform: translateY(-50px);
    }
}
</style>



